// ** Custom Components
import Avatar from '@components/avatar'
import { Link } from 'react-router-dom'
// ** Renders Client Columns

const renderClient = row => {

  return (
    <Avatar
      initials
      className='me-1'
      color={'light-primary'}
      content={row.name.charAt(0) || 'Jhone'}
    />
  )

}

const msToTime = (ms) => {

  let seconds = ms / 1000
  const hours = parseInt(seconds / 3600)
  seconds = seconds % 3600
  const minutes = parseInt(seconds / 60)
  seconds = seconds % 60
  return `${hours} Hrs : ${minutes} Mins`
}

const findHours = (ms) => {

  const seconds = ms / 1000
  const hours = parseInt(seconds / 3600)
  return hours

}

export const columns = [
  {
    name: 'Team Members',
    sortable: true,
    minWidth: '300px',
    sortField: 'username',
    selector: row => row.name,
    cell: row => (
      <div className='d-flex justify-content-left align-items-center'>

        {renderClient(row)}
        <div className='d-flex flex-column'>
          <Link to={`/reports/attendance/${row.id}`} className='cursor-pointer'>
            <small className='text-truncate text-muted mb-0'>{row.name}</small>
          </Link>
        </div>
      </div>
    )
  },
  {
    name: 'No Of Days',
    minWidth: '172px',
    cell: row => <span>{row.noOfDays}</span>
  },
  {
    name: 'Total Hours',
    minWidth: '138px',
    cell: row => <span>{msToTime(row.totalHous)}</span>
  },
  {
    name: 'Average Hours Per Day',
    minWidth: '230px',
    cell: row => <span className='text-capitalize'>{row.noOfDays > 0 ? parseFloat(findHours(row.totalHous) / parseFloat(row.noOfDays)).toFixed(2) : 0}</span>
  }
]
