// ** React Imports
import { Fragment, useEffect, useState } from 'react'

// ** Table Columns
import { columns } from './columns'

// ** Store & Actions
import { getMonthlySummary } from '@src/views/apps/attendance/store/index.js'
import { useDispatch, useSelector } from 'react-redux'

// ** Third Party Components
import ReactPaginate from 'react-paginate'
import DataTable from 'react-data-table-component'
import { ChevronDown, Share, Printer, File, Grid, Copy, ChevronRight, ChevronLeft, MoreVertical } from 'react-feather'

import { orgUserId } from '@src/helper/sassHelper'
const userId = orgUserId()

// ** Reactstrap Imports
import {
    Row,
    Col,
    Card,
    Input,
    DropdownMenu,
    DropdownItem,
    DropdownToggle,
    UncontrolledDropdown,
    CardBody
} from 'reactstrap'

// ** Styles
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import moment from 'moment'

// ** Table Header
const CustomHeader = ({ handlePerPage, rowsPerPage, changeDate, startDate }) => {
    // ** Converts table to CSV

    return (
        <div className='invoice-list-table-header w-100 me-1 ms-50 mt-2 mb-75'>
            <Row>
                <Col xl='6' md='6'  sm='6' className='d-flex align-items-center p-0'>
                    <div className='d-flex align-items-center w-100'>
                        <label htmlFor='rows-per-page'>Show</label>
                        <Input
                            className='mx-50'
                            type='select'
                            id='rows-per-page'
                            value={rowsPerPage}
                            onChange={handlePerPage}
                            style={{ width: '5rem' }}
                        >
                            <option value='10'>10</option>
                            <option value='25'>25</option>
                            <option value='50'>50</option>
                        </Input>
                        <label htmlFor='rows-per-page'>Entries</label>
                    </div>
                </Col>
                <Col
                    xl='6'
                    className='d-flex align-items-sm-center justify-content-xl-end justify-content-start flex-xl-nowrap flex-wrap flex-sm-row flex-column pe-xl-1 p-0 mt-xl-0 mt-1'
                >
                    <div className='border border-1 p-md-50'>
                        <ChevronLeft className='cursor-pointer' onClick={() => { changeDate(2) }} />
                        {moment.unix(startDate).format('MMMM')}
                        <ChevronRight className='cursor-pointer' onClick={() => { changeDate(1) }} />
                    </div>

                    <div className='d-flex align-items-center table-header-actions ms-1'>
                        <UncontrolledDropdown className='me-1'>
                            <DropdownToggle color='secondary' caret outline>
                                <MoreVertical className='font-small-4 me-50' />
                            </DropdownToggle>
                            <DropdownMenu>
                                <DropdownItem className='w-100'>
                                    <Grid className='font-small-4 me-50' />
                                    <span className='align-middle'>Excel</span>
                                </DropdownItem>
                            </DropdownMenu>
                        </UncontrolledDropdown>
                    </div>
                </Col>
            </Row>
        </div>
    )
}

const UsersList = () => {

    // ** Store Vars
    const dispatch = useDispatch()
    const store = useSelector(state => state.attendance)

    // ** States
    const [sort, setSort] = useState('desc')
    const [searchTerm, setSearchTerm] = useState('')
    const [currentPage, setCurrentPage] = useState(1)
    const [sortColumn, setSortColumn] = useState('createdon')
    const [rowsPerPage, setRowsPerPage] = useState(10)
    const [currentStatus] = useState({ value: '', label: 'Select Status', number: 0 })
    const [startDate, setStartDate] = useState(moment().startOf('month').startOf('day').unix())
    const [endDate, setEndDate] = useState(moment().endOf('month').startOf('day').unix())

    // ** Get data on mount
    useEffect(() => {

        dispatch(
            getMonthlySummary({
                sort,
                sortColumn,
                q: searchTerm,
                page: currentPage,
                perPage: rowsPerPage,
                userId,
                status: currentStatus.value,
                start: startDate,
                end: endDate
            })
        )

    }, [startDate, endDate])

    // ** Function in get data on page change
    const handlePagination = page => {
        dispatch(
            getMonthlySummary({
                sort,
                sortColumn,
                q: searchTerm,
                perPage: rowsPerPage,
                page: page.selected + 1,
                userId,
                status: currentStatus.value,
                start: startDate,
                end: endDate
            })
        )
        setCurrentPage(page.selected + 1)
    }

    // ** Function in get data on rows per page
    const handlePerPage = e => {
        const value = parseInt(e.currentTarget.value)
        dispatch(
            getMonthlySummary({
                sort,
                sortColumn,
                q: searchTerm,
                perPage: value,
                page: currentPage,
                userId,
                status: currentStatus.value,
                start: startDate,
                end: endDate
            })
        )
        setRowsPerPage(value)
    }

    // ** Function in get data on search query change
    const handleFilter = val => {
        setSearchTerm(val)
        dispatch(
            getMonthlySummary({
                sort,
                q: val,
                sortColumn,
                page: currentPage,
                perPage: rowsPerPage,
                userId,
                status: currentStatus.value,
                start: startDate,
                end: endDate
            })
        )
    }

    // ** Custom Pagination
    const CustomPagination = () => {
        const count = Number(Math.ceil(store.total / rowsPerPage))

        return (
            <ReactPaginate
                previousLabel={''}
                nextLabel={''}
                pageCount={count || 1}
                activeClassName='active'
                forcePage={currentPage !== 0 ? currentPage - 1 : 0}
                onPageChange={page => handlePagination(page)}
                pageClassName={'page-item'}
                nextLinkClassName={'page-link'}
                nextClassName={'page-item next'}
                previousClassName={'page-item prev'}
                previousLinkClassName={'page-link'}
                pageLinkClassName={'page-link'}
                containerClassName={'pagination react-paginate justify-content-end my-2 pe-1'}
            />
        )
    }

    // ** Table data to render
    const dataToRender = () => {
        const filters = {
            userId,
            status: currentStatus.value,
            q: searchTerm
        }

        const isFiltered = Object.keys(filters).some(function (k) {
            return filters[k].length > 0
        })

        if (store.monthlySummaries.length > 0) {
            return store.monthlySummaries
        } else if (store.monthlySummaries.length === 0 && isFiltered) {
            return []
        } else {
            return store.allData.slice(0, rowsPerPage)
        }
    }

    const handleSort = (column, sortDirection) => {
        setSort(sortDirection)
        setSortColumn(column.sortField)
        dispatch(
            getMonthlySummary({
                sort,
                sortColumn,
                q: searchTerm,
                page: currentPage,
                perPage: rowsPerPage,
                userId,
                status: currentStatus.value,
                start: startDate,
                end: endDate
            })
        )
    }
    const changeDate = (val) => {
        let start_date, end_date
        if (val === 1) {
            start_date = moment(moment.unix(startDate)).add(1, 'M').startOf('day').unix()
            end_date = moment(moment.unix(endDate)).add(1, 'M').endOf('day').unix()
        } else {
            start_date = moment(moment.unix(startDate)).subtract(1, 'M').startOf('day').unix()
            end_date = moment(moment.unix(endDate)).subtract(1, 'M').endOf('day').unix()
        }
        setStartDate(start_date)
        setEndDate(end_date)
    }
    return (
        <Fragment>
            <Card className='overflow-hidden'>
                <div className='react-dataTable'>
                    <DataTable
                        noHeader
                        subHeader
                        sortServer
                        pagination
                        responsive
                        paginationServer
                        columns={columns}
                        onSort={handleSort}
                        sortIcon={<ChevronDown />}
                        className='react-dataTable'
                        paginationComponent={CustomPagination}
                        data={dataToRender()}
                        subHeaderComponent={
                            <CustomHeader
                                store={store}
                                searchTerm={searchTerm}
                                rowsPerPage={rowsPerPage}
                                handleFilter={handleFilter}
                                handlePerPage={handlePerPage}
                                changeDate={changeDate}
                                startDate={startDate}
                            />
                        }
                    />
                </div>
            </Card>
        </Fragment>
    )
}

export default UsersList
